body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  width: 100%;
}

.main-nav {
  display: flex;
  background: #87cefa;
  width: 100%;
  color: #fff;
  padding-left: 20px;
}

div {
  box-sizing: border-box;
}
.list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 20px 20px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.list .col-1 h4 {
  color: #333;
  cursor: pointer;
}

.list .col-1 span {
  cursor: pointer;
}

.list .col-2 {
  width: 90px;
  margin-top: 15px;
}

.list .col-2 p {
  color: #aaa;
  font-size: 12px;
  text-align: center;
}

.list .col-2 button {
  width: 70px;
  padding: 10px 10px;
  border: none;
  border-radius: 10px;
  background: #eee;
  color: #ccc;
  cursor: pointer;
  display: block;
  margin: 0 auto;
}

.add_story {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.add_story input,
.add_story input:focus {
  border: 2px solid lightpink;
  border-radius: 10px;
  width: 200px;
  height: 40px;
  padding-left: 10px;
  color: #333;
  outline: none;
}

.add_story button {
  width: 70px;
  padding: 10px 10px;
  border: none;
  background: none;
  color: #87cefa;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

